import React, { PureComponent } from 'react';
import './App.scss'
import './common/style/reset.css'
// iconfont
import './common/iconfont/iconfont.css'
// flexiable
import './common/lib/simple-flexible'
// import 'amfe-flexible'
import { Provider } from 'react-redux'
import { createStore } from 'redux'
import newsAPPreducer from './reducer/reducer'

import AppHeader from './smart/AppHeader'
import AppBody from './smart/AppBody'


// import {getNews} from './common/API/getNews'
let store = createStore(newsAPPreducer,
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
)

class App extends PureComponent {
  render() {
    return (
      <Provider store={store}>
        <div className="app">
          <AppHeader />
          <AppBody />
        </div>
      </Provider>
    );
  }
}

export default App;



