import * as actions from './actionTypes';

export function CHANGE_COUNTRY (country) {
    return {
        type: actions.CHANGE_COUNTRY,
        country
    }
}

export function CHANGE_CATEGORY (category) {
    return {
        type: actions.CHANGE_CATEGORY,
        category
    }
}

export function UPDATE_NEWS(news) {
    return {
        type: actions.UPDATE_NEWS,
        news
    }
}

export function LOAD_MORE_NEWS(news) {
    return {
        type: actions.LOAD_MORE_NEWS,
        news
    }
}

export function UPDATE_PAGE(page) {
    return {
        type: actions.UPDATE_PAGE,
        page
    }
}

export function RESET_PAGE() {
    return {
        type: actions.RESET_PAGE
    }
}


