import { combineReducers } from 'redux'
import * as actions from '../acitons/actionTypes'

function change_country_reducer(state = 'cn', action) {
    switch(action.type) {
        case actions.CHANGE_COUNTRY:
            return action.country
        default:
            return state
    }
}

function change_category_reducer(state = 'business', action) {
    switch(action.type) {
        case actions.CHANGE_CATEGORY:
            return action.category
        default:
            return state
    }
}

function get_news_reducer(state = [], action) {
    switch(action.type) {
        case actions.UPDATE_NEWS: 
            return [...action.news]
        case actions.LOAD_MORE_NEWS:
            return [...state, ...action.news]
        default: 
            return state
    }
}

function change_page_reducer(state = 1, action) {
    switch(action.type) {
        case actions.UPDATE_PAGE:
            return action.page
        case actions.RESET_PAGE:
            return 1
        default:
            return state
    }
}

export default combineReducers({
    country: change_country_reducer,
    category: change_category_reducer,
    news: get_news_reducer,
    page: change_page_reducer
})


