import { CHANGE_COUNTRY, CHANGE_CATEGORY, UPDATE_NEWS, RESET_PAGE } from '../acitons/actions'
import { getNews } from '../common/API/getNews'
import { AppLogo } from '../dumb/AppLogo/AppLogo'
import { AppMenu } from '../dumb/AppMenu/AppMenu'
import { AppCountry } from '../dumb/AppCountry/AppCountry'
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';

class AppHeader extends PureComponent {
    constructor(props) {
        super()
        this.selectCountry = this.selectCountry.bind(this)
        this.selectCategory = this.selectCategory.bind(this)
    }
    render() {
        return (
            <header>
                <AppLogo></AppLogo>
                <div style={{
                    display: 'flex',
                    justifyContent: 'space-around',
                    alignItems: 'center'
                }}>
                    <AppMenu selectCategory={this.selectCategory} category={this.props.category} />
                    <AppCountry selectCountry={this.selectCountry} country={this.props.country} />
                </div>
            </header>
        )
    }
    selectCountry(country) {
        this.props.selectCountry(country)
        // dispatch country
        setTimeout(async () => {
            let res = await getNews(this.props)
            this.props.updateNews(res.data.articles)
            this.props.resetPage()
        }, 0)
    }
    selectCategory(category) {
        this.props.selectCategory(category)
        setTimeout(async () => {
            let res = await getNews(this.props)
            this.props.updateNews(res.data.articles)
            this.props.resetPage()
        }, 0)
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        // 
        selectCountry: (country) => {
            dispatch(CHANGE_COUNTRY(country))
        },
        // 
        selectCategory: (location) => {
            dispatch(CHANGE_CATEGORY(location))
        },
        // 
        updateNews: (news) => {
            dispatch(UPDATE_NEWS(news))
        },
        // 
        resetPage:() => {
            dispatch(RESET_PAGE())
        }
    }
}

const mapStateToProps = (state, ownProps) => {
    return state
}

export default connect(mapStateToProps, mapDispatchToProps)(AppHeader)



