import React, { PureComponent } from 'react'
import { Carousel } from "antd-mobile";
import 'antd-mobile/lib/carousel/style/css'
import './NewsCarousel.scss'

export class NewsCarousel extends PureComponent {
    render() {
        return (
            <Carousel autoplay={true} infinite className='news-carousel'>
                {
                   this.props.news.filter((item, index) => {
                       return item.urlToImage
                   }).slice(0, 5).map((item, index) => {
                       return (
                        <div className='carousel-item' key={item.url}>
                           <img src={item.urlToImage} alt="" key={item.urlToImage} onError={this.fixImageError.bind(this)} className='carousel-image' onClick={this.checkDetail.bind(this, item)}/>
                           <h6 className='carousel-title'>{item.title}</h6>
                        </div>
                       )        
                   })
                }
            </Carousel>
        )
    }
    checkDetail(news){
        window.open(news.url)
    }
    fixImageError(event){
        event.target.src = 'http://ww1.sinaimg.cn/large/e3507465gy1fs1jwb6whlj20lc0c0myi.jpg'
    }
}


