import React, { PureComponent } from 'react'
import BetterScroll from 'better-scroll'
import './AppMenu.scss'

const categoryList = [
    {
        key: 'business',
        title: ''
    }, {
        key: 'entertainment',
        title: ''
    }, {
        key: 'general',
        title: ''
    }, {
        key: 'health',
        title: ''
    }, {
        key: 'science',
        title: ''
    }, {
        key: 'sports',
        title: ''
    }, {
        key: 'technology',
        title: ''
    }
]

export class AppMenu extends PureComponent {
    constructor() {
        super()
        this.state = {
            menuScroll: null
        }
    }
    render() {
        return (
            <div ref='menu-container' className='menu-container'>
                <ul className='menu-content' style={{width: this.state.maxWidth + 'px'}}>
                    {
                        categoryList.map((category, index) => {
                            return (
                                <li className={category.key === this.props.category ? 'item-active menu-item' : 'menu-item'} key={category.key} onClick={this.selectCategory.bind(this, category)}>{category.title}</li>
                            )
                        })
                    }
                </ul>
            </div>
        )
    }
    componentDidMount() {
        this.setState((prevState, props) => {
            return {
                menuScroll: new BetterScroll(this.refs['menu-container'], {
                    click: true,
                    scrollX: true
                })
            }
        })
        this._handleScroll()
    }
    _handleScroll(){
        var menuItems = document.getElementsByClassName('menu-item')
        var maxWidth = 0
        Array.prototype.forEach.call(menuItems, (menuItem, index) => {
            maxWidth += menuItem.offsetWidth
        })
        this.setState({
            maxWidth
        })
    }
    selectCategory(category) {
        this.props.selectCategory(category.key)
    }
}


