export class AppCountry extends PureComponent {
    constructor(props){
        super()
        this.state = {
            defaultText: this._findText(props.country)
        }
        this._findText = this._findText.bind(this)
    }
    render() {
        return (
            <Picker data={countryList} cols={1} onChange={this.selectCountry.bind(this)} className='selector'>
                <List.Item arrow="horizontal" className='selector-item'>{this.state.defaultText}</List.Item>
            </Picker>
        )
    }
    selectCountry(value) {
        this.props.selectCountry(value[0])
        this.setState({
            defaultText: this._findText(value[0])
        })
    }
    _findText(value){
        var result  = countryList.find((item, index) => {
            return item.value === value
        })
        return result.label
    }
}


