import { NewsCarousel } from '../dumb/NewsCarousel/NewsCarousel'
import { NewsList } from '../dumb/NewsList/NewsList'
import { UPDATE_NEWS, LOAD_MORE_NEWS, UPDATE_PAGE, RESET_PAGE } from '../acitons/actions'
import { connect } from 'react-redux'
import { getNews } from '../common/API/getNews'
import React, { PureComponent } from 'react'

const mapStateToProps = (state, ownprops) => {
    return state
}

const mapDispatchToProps = (dispatch, onwprops) => {
    return {
        updateNews: (news) => {
            dispatch(UPDATE_NEWS(news))
        },
        loadMoreNews: (news) => {
            dispatch(LOAD_MORE_NEWS(news))
        },
        updatePage: (page) => {
            dispatch(UPDATE_PAGE(page))
        },
        resetPage: () => {
            dispatch(RESET_PAGE())
        }
    }
}

class AppBody extends PureComponent {
    render() {
        return (
            <section className='app-body'>
                <NewsCarousel news={this.props.news} />
                <NewsList news={this.props.news} loadMoreNews={this.loadMoreNews.bind(this)} page={this.props.page}updatePage={this.updatePage.bind(this)}/>
            </section>
        )
    }
    async componentDidMount() {
        let res = await getNews(this.props)
        this.props.updateNews(res.data.articles)
    }
    async loadMoreNews(page) {
        var res = await getNews({...this.props, page})
        this.props.loadMoreNews(res.data.articles)
    }
    updatePage(page){
        this.props.updatePage(page)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AppBody)


