import React, { PureComponent } from 'react'
import { PullToRefresh } from 'antd-mobile'
import './NewsList.scss'

export class NewsList extends PureComponent {
    render() {
        return (
            <PullToRefresh direction='up' onRefresh={this.loadMore.bind(this)}>
                <ul className='news-list'>
                    {
                        this.props.news.map((news, index) => {
                            return (
                                <li className='news-item' key={index} onClick={this.checkDetail.bind(this, news)}>
                                    {
                                        news.urlToImage ? <div className='item-left'>
                                            <img src={news.urlToImage} alt="" className='news-image' onError={this._fixImageError.bind(this)} />
                                        </div> : ''
                                    }
                                    <div className='item-right'>
                                        <h3 className='news-title'>{news.title}</h3>
                                        <p className='news-desc'>{news.description || ''}</p>
                                        <span className='news-from'>{news.publishedAt}  &nbsp;&nbsp;&nbsp;:{news.author ? news.author : ''}</span>
                                    </div>
                                </li>
                            )
                        })
                    }
                </ul>
            </PullToRefresh>
        )
    }
    checkDetail(news) {
        window.open(news.url)
    }
    _fixImageError(event) {
        event.target.src = 'http://ww1.sinaimg.cn/large/e3507465gy1fs1jwb6whlj20lc0c0myi.jpg'
    }
    loadMore(){
        this.props.updatePage(this.props.page + 1)
        this.props.loadMoreNews(this.props.page)
    }
}


