import axios from 'axios'

const APPKEY = 'a0229e7e584b40ff867e1c3251a9de2a'

export function getNews({country, category, page = 1}) {
    return axios({
        url: 'https://newsapi.org/v2/top-headlines',
        method: 'GET',
        params: {
            apiKey: APPKEY,
            country,
            category,
            pageSize: 10,
            page
        }
    })
}


