import React from 'react'
import './Applogo.scss'

export function AppLogo() {
    return (
        <div className='app-logo'>
            <i className='iconfont icon-newspaper app-icon'></i>
            <h1 className='app-name'>Breaking News</h1>
        </div>
    )
}


